import PropTypes from 'prop-types';
import { Icon } from '@iconify/react';

import { paramCase } from 'change-case';
import { Link as RouterLink } from 'react-router-dom';
// material
import { Box, Card, Link, Typography, Stack, Tooltip } from '@material-ui/core';
import { styled } from '@material-ui/core/styles';
// routes
import { PATH_DASHBOARD } from '../../../../routes/paths';
//
import Label from '../../../Label';

// ----------------------------------------------------------------------

const ProductImgStyle = styled('img')({
  top: 0,
  width: '100%',
  height: '100%',
  objectFit: 'cover',
  position: 'absolute'
});

// ----------------------------------------------------------------------

ShopProductCard.propTypes = {
  product: PropTypes.object
};

export default function ShopProductCard({ product }) {
  const { name, cover, price, status, priceSale } = product;
  const linkTo = `${PATH_DASHBOARD.dapp.nftgalleryone}/product/${paramCase(name)}`;

  return (
    <Card>
      <Box sx={{ pt: '50%', position: 'relative' }}>
        {status && (
          <Label
            variant="filled"
            color={(status === 'sale' && 'error') || 'info'}
            sx={{
              top: 16,
              right: 16,
              zIndex: 9,
              position: 'absolute',
              textTransform: 'uppercase'
            }}
          >
            {status}
          </Label>
        )}
        <ProductImgStyle alt={name} src={cover} />
      </Box>

      <Stack spacing={1} sx={{ p: 2, pt: 1, pb: 1 }}>
        <Stack direction="row" alignItems="center" justifyContent="space-between">
          <Link to={linkTo} color="inherit" component={RouterLink} underline="none">
            <Typography variant="subtitle2" noWrap>
              {name}
            </Typography>
          </Link>
          <Stack spacing={0}>
            <Stack direction="row" alignItems="center" spacing={1}>
              <Icon icon="logos:ethereum" width={16} height={16} />
              <Typography variant="subtitle2" noWrap>
                0.01
              </Typography>
            </Stack>
          </Stack>
        </Stack>

        <Stack direction="row" justifyContent="space-between">
          <Tooltip title="NFT Contract">
            <Link href="#" underline="none" target="_blank" rel="noopener">
              <Stack direction="row" alignItems="center" spacing={0.5}>
                <Icon icon="teenyicons:contract-outline" width={16} height={16} />
                <Typography variant="body2" noWrap>
                  0xE53821
                </Typography>
              </Stack>
            </Link>
          </Tooltip>
          <Tooltip title="NFT ID">
            <Link href="#" underline="none" target="_blank" rel="noopener">
              <Stack direction="row" alignItems="center" spacing={0.5}>
                <Icon icon="ant-design:number-outlined" width={16} height={16} />
                <Typography variant="body2" noWrap>
                  142
                </Typography>
              </Stack>
            </Link>
          </Tooltip>
          <Tooltip title="Author Address">
            <Link href="#" underline="none" target="_blank" rel="noopener">
              <Stack direction="row" alignItems="center" spacing={0.5}>
                <Icon icon="bi:shield-check" width={16} height={16} />
                <Typography variant="body2" noWrap>
                  0x1432
                </Typography>
              </Stack>
            </Link>
          </Tooltip>
        </Stack>
      </Stack>
    </Card>
  );
}
